/**
 * Recursively normalize market data to ensure all required fields are present with correct types
 */
function normalizeMarketData(data: any): any {
    if (Array.isArray(data)) {
        return data.map(item => normalizeMarketData(item));
    }
    
    if (data && typeof data === 'object') {
        const normalized = { ...data };
        
        // Normalize individual market object - this is the key fix
        if (normalized.id || normalized.question || normalized.tokenIds) {
            normalized.acceptingOrders = normalized.acceptingOrders !== undefined 
                ? Boolean(normalized.acceptingOrders) 
                : true; // Default to true if undefined
            
            normalized.active = normalized.active !== undefined 
                ? Boolean(normalized.active) 
                : true;
            
            normalized.closed = normalized.closed !== undefined 
                ? Boolean(normalized.closed) 
                : false;
            
            // Ensure numeric fields are properly typed
            if (normalized.volume !== undefined) {
                normalized.volume = typeof normalized.volume === 'number' 
                    ? normalized.volume 
                    : parseFloat(normalized.volume) || 0;
            }
            
            if (normalized.liquidity !== undefined) {
                normalized.liquidity = typeof normalized.liquidity === 'number' 
                    ? normalized.liquidity 
                    : parseFloat(normalized.liquidity) || 0;
            }
            
            // Ensure string fields are properly typed
            if (normalized.id !== undefined) normalized.id = String(normalized.id);
            if (normalized.question !== undefined) normalized.question = String(normalized.question);
            if (normalized.description !== undefined) normalized.description = String(normalized.description);
            
            // Ensure array fields are properly typed
            if (normalized.outcomes !== undefined && !Array.isArray(normalized.outcomes)) {
                normalized.outcomes = [];
            }
            if (normalized.prices !== undefined && !Array.isArray(normalized.prices)) {
                normalized.prices = [];
            }
            if (normalized.tokenIds !== undefined && !Array.isArray(normalized.tokenIds)) {
                normalized.tokenIds = [];
            }
            
            // Handle dates
            if (normalized.endDate) {
                try {
                    normalized.endDate = new Date(normalized.endDate).toISOString();
                } catch (e) {
                    normalized.endDate = null;
                }
            }
            if (normalized.startDate) {
                try {
                    normalized.startDate = new Date(normalized.startDate).toISOString();
                } catch (e) {
                    normalized.startDate = null;
                }
            }
        }
        
        // Handle nested markets array (for events)
        if (normalized.markets && Array.isArray(normalized.markets)) {
            normalized.markets = normalized.markets.map((market: any) => normalizeMarketData(market));
        }
        
        // Recursively normalize other nested objects
        Object.keys(normalized).forEach(key => {
            if (key !== 'markets' && normalized[key] && typeof normalized[key] === 'object' && !Array.isArray(normalized[key])) {
                normalized[key] = normalizeMarketData(normalized[key]);
            }
        });
        
        return normalized;
    }
    
    return data;
}

