const initializeActions = async (character?: Character) => {
    try {
        // Validate environment variables for different approaches
        const apiKey = character?.settings?.secrets?.POLYMARKET_API_KEY || process.env.POLYMARKET_API_KEY;
        const apiSecret = character?.settings?.secrets?.POLYMARKET_SECRET || process.env.POLYMARKET_SECRET;
        const apiPassphrase = character?.settings?.secrets?.POLYMARKET_PASSPHRASE || process.env.POLYMARKET_PASSPHRASE;
        const walletPrivateKey = character?.settings?.secrets?.WALLET_PRIVATE_KEY || process.env.WALLET_PRIVATE_KEY;
        const rpcProviderUrl = character?.settings?.secrets?.RPC_PROVIDER_URL || process.env.RPC_PROVIDER_URL;

        // Check wallet configuration
        if (!walletPrivateKey || !rpcProviderUrl) {
            console.warn("‚ö†Ô∏è Missing wallet configuration - Polymarket plugin will not be available");
            console.log("üìù Please set WALLET_PRIVATE_KEY and RPC_PROVIDER_URL environment variables");
            return [];
        }

        let actions: Action[] = [];

        // Try to initialize direct API actions first (more reliable for market data)
        try {
            console.log("üîÑ Initializing direct API actions...");
            const directApiActions = await getPolymarketActions();
            actions = [...actions, ...directApiActions];
            console.log(`‚úî Added ${directApiActions.length} direct API actions`);
            
            if (!apiKey || !apiSecret || !apiPassphrase) {
                console.log("üìù Trading functions require POLYMARKET_API_KEY, POLYMARKET_SECRET, and POLYMARKET_PASSPHRASE");
                console.log("üìà Market data retrieval is available via direct API calls");
            } else {
                console.log("üîë Full trading functionality available via direct API");
            }
        } catch (directApiError) {
            console.warn("‚ö†Ô∏è Failed to initialize direct API actions:", directApiError);
        }

        // Try to initialize GOAT SDK actions (for advanced trading features)
        if (apiKey && apiSecret && apiPassphrase) {
            try {
                console.log("üîÑ Initializing GOAT SDK actions...");
                const goatActions = createGoatPolymarketActions();
                actions = [...actions, ...goatActions];
                console.log(`‚úî Added ${goatActions.length} GOAT SDK actions`);
                console.log("üöÄ Full GOAT SDK functionality available");
            } catch (goatError) {
                console.warn("‚ö†Ô∏è Failed to initialize GOAT SDK actions:", goatError);
                console.log("üìù GOAT SDK features will not be available");
            }
        } else {
            console.log("üìù GOAT SDK features require full API credentials");
        }

        if (actions.length === 0) {
            console.warn("‚ö†Ô∏è No Polymarket actions could be initialized");
            return [];
        }

        console.log(`‚úî Polymarket plugin initialized successfully with ${actions.length} total actions`);
        return actions;

    } catch (error) {
        console.error("‚ùå Failed to initialize Polymarket actions:", error);
        return []; // Return empty array instead of failing
    }
};

export const polymarketPlugin: Plugin = {
    name: "[Polymarket] Integration",
    description: "Polymarket prediction market integration plugin with hybrid API support (Direct API + GOAT SDK)",
    providers: [walletProvider],
    evaluators: [],
    services: [],
    actions: [], // Initialize as empty array, will be populated when plugin is loaded
    handlePostCharacterLoaded: async (character: Character): Promise<Character> => {
        // Initialize actions when character is loaded with character context
        const actions = await initializeActions(character);
        polymarketPlugin.actions = actions;
        return character;
    }
};

// Initialize actions when plugin is loaded (fallback for backward compatibility)
initializeActions().then(actions => {
    if (polymarketPlugin.actions && polymarketPlugin.actions.length === 0) {
        polymarketPlugin.actions = actions;
    }
}).catch(error => {
    console.error("Failed to initialize Polymarket plugin actions:", error);
    if (polymarketPlugin.actions && polymarketPlugin.actions.length === 0) {
        polymarketPlugin.actions = [];
    }
});

export default polymarketPlugin;