import {
    type Action,
    generateText,
    type HandlerCallback,
    type IAgentRuntime,
    type Memory,
    ModelClass,
    type State,
    composeContext,
    generateObject,
} from "@elizaos/core";
import { z } from "zod";
import { getPolymarketClient } from "./provider";

// Add API client for direct Polymarket API calls
async function fetchPolymarketAPI(endpoint: string, options: RequestInit = {}) {
    const baseUrl = 'https://gamma-api.polymarket.com';
    const url = `${baseUrl}${endpoint}`;
    
    const response = await fetch(url, {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json',
            ...options.headers,
        },
        ...options,
    });
    
    if (!response.ok) {
        throw new Error(`Polymarket API error: ${response.status} ${response.statusText}`);
    }
    
    return response.json();
}

/**
 * Get all Polymarket actions
 */
export async function getPolymarketActions(): Promise<Action[]> {
    const actions: Action[] = [
        {
            name: "GET_POLYMARKET_EVENTS",
            similes: ["LIST_EVENTS", "SHOW_EVENTS", "VIEW_EVENTS", "GET_MARKETS"],
            description: "Get Polymarket events and markets",
            validate: async () => true,
            handler: async (
                runtime: IAgentRuntime,
                message: Memory,
                state: State | undefined,
                options?: Record<string, unknown>,
                callback?: HandlerCallback
            ): Promise<boolean> => {
                try {
                    let currentState = state ?? (await runtime.composeState(message));
                    currentState = await runtime.updateRecentMessageState(currentState);

                    const eventsContext = composeContext({
                        state: currentState,
                        template: `{{recentMessages}}

Extract any specific event or market criteria from the recent messages. Look for:
- Event names or keywords
- Date ranges
- Market categories
- Active/inactive status
- Limit/count preferences
If no specific criteria is mentioned, get general events.`
                    });

                    const { object: params } = await generateObject({
                        runtime,
                        context: eventsContext,
                        modelClass: ModelClass.LARGE,
                        schema: z.object({
                            limit: z.number().describe("Maximum number of events to return").default(20),
                            active: z.boolean().describe("Filter for active events only").default(true),
                            order: z.enum(["desc", "asc"]).describe("Sort order").default("desc"),
                            offset: z.number().describe("Number of results to skip").default(0)
                        })
                    });

                    // Type params
                    const typedParams = params as { limit?: number; active?: boolean; order?: string; offset?: number };
                    // Build query parameters
                    const queryParams = new URLSearchParams();
                    if (typedParams.limit) queryParams.append('limit', typedParams.limit.toString());
                    if (typedParams.active !== undefined) queryParams.append('active', typedParams.active ? 'true' : 'false');
                    if (typedParams.order) queryParams.append('order', typedParams.order);
                    if (typedParams.offset) queryParams.append('offset', typedParams.offset.toString());

                    const endpoint = `/markets${queryParams.toString() ? '?' + queryParams.toString() : ''}`;
                    const result = await fetchPolymarketAPI(endpoint);

                    const responseText = await generateResponse(
                        runtime,
                        currentState,
                        "GET_POLYMARKET_EVENTS",
                        result,
                        "Retrieved Polymarket events and markets successfully"
                    );

                    callback?.({ text: responseText, content: result });
                    return true;
                } catch (error) {
                    return handleError(error, "GET_POLYMARKET_EVENTS", callback);
                }
            },
            examples: [
                [
                    {
                        user: "{{user1}}",
                        content: { text: "Show me the latest Polymarket events" }
                    },
                    {
                        user: "{{user2}}",
                        content: { text: "Here are the latest Polymarket events and markets..." }
                    }
                ],
                [
                    {
                        user: "{{user1}}",
                        content: { text: "Get 10 active markets from Polymarket" }
                    },
                    {
                        user: "{{user2}}",
                        content: { text: "I've retrieved 10 active markets from Polymarket..." }
                    }
                ]
            ]
        },
        {
            name: "GET_MARKET_INFO",
            similes: ["MARKET_DETAILS", "SHOW_MARKET", "VIEW_MARKET", "MARKET_INFO"],
            description: "Get detailed information about a specific market",
            validate: async () => true,
            handler: async (
                runtime: IAgentRuntime,
                message: Memory,
                state: State | undefined,
                options?: Record<string, unknown>,
                callback?: HandlerCallback
            ): Promise<boolean> => {
                try {
                    let currentState = state ?? (await runtime.composeState(message));
                    currentState = await runtime.updateRecentMessageState(currentState);

                    const marketContext = composeContext({
                        state: currentState,
                        template: `{{recentMessages}}

Extract the market identifier from the recent messages. Look for:
- Market ID
- Market slug
- Market name or title
- Event name
- Token address`
                    });

                    const { object: params } = await generateObject({
                        runtime,
                        context: marketContext,
                        modelClass: ModelClass.LARGE,
                        schema: z.object({
                            marketId: z.string().describe("Market ID or slug identifier")
                        })
                    });

                    const typedParams = params as { marketId: string };
                    const endpoint = `/markets/${typedParams.marketId}`;
                    const result = await fetchPolymarketAPI(endpoint);

                    const responseText = await generateResponse(
                        runtime,
                        currentState,
                        "GET_MARKET_INFO",
                        result,
                        "Retrieved market information successfully"
                    );

                    callback?.({ text: responseText, content: result });
                    return true;
                } catch (error) {
                    return handleError(error, "GET_MARKET_INFO", callback);
                }
            },
            examples: [
                [
                    {
                        user: "{{user1}}",
                        content: { text: "Get info for market ID 12345" }
                    },
                    {
                        user: "{{user2}}",
                        content: { text: "Here's the market information..." }
                    }
                ]
            ]
        },
        {
            name: "SEARCH_MARKETS",
            similes: ["SEARCH", "FIND_MARKETS", "LOOK_FOR_MARKETS"],
            description: "Search for markets by query",
            validate: async () => true,
            handler: async (
                runtime: IAgentRuntime,
                message: Memory,
                state: State | undefined,
                options?: Record<string, unknown>,
                callback?: HandlerCallback
            ): Promise<boolean> => {
                try {
                    let currentState = state ?? (await runtime.composeState(message));
                    currentState = await runtime.updateRecentMessageState(currentState);

                    const searchContext = composeContext({
                        state: currentState,
                        template: `{{recentMessages}}

Extract search criteria from the recent messages. Look for:
- Search terms or keywords
- Topic names
- Event categories
- Specific market names`
                    });

                    const { object: params } = await generateObject({
                        runtime,
                        context: searchContext,
                        modelClass: ModelClass.LARGE,
                        schema: z.object({
                            q: z.string().describe("Search query"),
                            limit: z.number().describe("Maximum number of results").default(20)
                        })
                    });

                    const typedParams = params as { q: string; limit?: number };
                    const queryParams = new URLSearchParams();
                    queryParams.append('q', typedParams.q);
                    if (typedParams.limit) queryParams.append('limit', typedParams.limit.toString());

                    const endpoint = `/markets?${queryParams.toString()}`;
                    const result = await fetchPolymarketAPI(endpoint);

                    const responseText = await generateResponse(
                        runtime,
                        currentState,
                        "SEARCH_MARKETS",
                        result,
                        `Found markets matching "${typedParams.q}"`
                    );

                    callback?.({ text: responseText, content: result });
                    return true;
                } catch (error) {
                    return handleError(error, "SEARCH_MARKETS", callback);
                }
            },
            examples: [
                [
                    {
                        user: "{{user1}}",
                        content: { text: "Search for markets about Trump" }
                    },
                    {
                        user: "{{user2}}",
                        content: { text: "I found several markets related to Trump..." }
                    }
                ]
            ]
        },
        {
            name: "CREATE_ORDER",
            similes: ["PLACE_ORDER", "BUY", "SELL", "TRADE", "BET"],
            description: "Create a buy or sell order on Polymarket",
            validate: async () => true,
            handler: async (
                runtime: IAgentRuntime,
                message: Memory,
                state: State | undefined,
                options?: Record<string, unknown>,
                callback?: HandlerCallback
            ): Promise<boolean> => {
                try {
                    const { tools } = await getPolymarketClient();
                    
                    let currentState = state ?? (await runtime.composeState(message));
                    currentState = await runtime.updateRecentMessageState(currentState);

                    const orderContext = composeContext({
                        state: currentState,
                        template: `{{recentMessages}}

Extract order parameters from the recent messages. Look for:
- Order type (buy/sell)
- Market ID or token address
- Amount or quantity
- Price
- Side (YES/NO)`
                    });

                    const { object: params } = await generateObject({
                        runtime,
                        context: orderContext,
                        modelClass: ModelClass.LARGE,
                        schema: z.object({
                            tokenId: z.string().describe("Token ID for the market"),
                            side: z.enum(["BUY", "SELL"]).describe("Order side"),
                            amount: z.string().describe("Amount to trade"),
                            price: z.string().describe("Price per token"),
                            outcome: z.enum(["YES", "NO"]).describe("Outcome to bet on").optional()
                        })
                    });

                    // Type params
                    const typedParams = params as { tokenId: string; side: "BUY"|"SELL"; amount: string; price: string; outcome?: "YES"|"NO" };

                    const orderTool = tools.find(tool => 
                        tool.name?.toLowerCase().includes('order') ||
                        tool.name?.toLowerCase().includes('trade')
                    );

                    if (!orderTool) {
                        throw new Error("Order tool not found");
                    }

                    const result = await orderTool.execute?.(typedParams) || await orderTool.call?.(typedParams);

                    const responseText = await generateResponse(
                        runtime,
                        currentState,
                        "CREATE_ORDER",
                        result,
                        "Order created successfully"
                    );

                    callback?.({ text: responseText, content: result });
                    return true;
                } catch (error) {
                    return handleError(error, "CREATE_ORDER", callback);
                }
            },
            examples: [
                [
                    {
                        user: "{{user1}}",
                        content: { text: "Buy 10 YES tokens at 0.60 for market 12345" }
                    },
                    {
                        user: "{{user2}}",
                        content: { text: "Created buy order for 10 YES tokens at 0.60..." }
                    }
                ]
            ]
        },
        {
            name: "GET_ACTIVE_ORDERS",
            similes: ["LIST_ORDERS", "SHOW_ORDERS", "VIEW_ORDERS", "MY_ORDERS"],
            description: "Get all active orders for the wallet",
            validate: async () => true,
            handler: async (
                runtime: IAgentRuntime,
                message: Memory,
                state: State | undefined,
                options?: Record<string, unknown>,
                callback?: HandlerCallback
            ): Promise<boolean> => {
                try {
                    const { tools } = await getPolymarketClient();
                    
                    let currentState = state ?? (await runtime.composeState(message));
                    currentState = await runtime.updateRecentMessageState(currentState);

                    const ordersTool = tools.find(tool => 
                        tool.name?.toLowerCase().includes('orders') ||
                        (tool.name?.toLowerCase().includes('get') && tool.name?.toLowerCase().includes('order'))
                    );

                    if (!ordersTool) {
                        throw new Error("Get orders tool not found");
                    }

                    const result = await ordersTool.execute?.({}) || await ordersTool.call?.({});

                    const responseText = await generateResponse(
                        runtime,
                        currentState,
                        "GET_ACTIVE_ORDERS",
                        result,
                        "Retrieved active orders successfully"
                    );

                    callback?.({ text: responseText, content: result });
                    return true;
                } catch (error) {
                    return handleError(error, "GET_ACTIVE_ORDERS", callback);
                }
            },
            examples: [
                [
                    {
                        user: "{{user1}}",
                        content: { text: "Show me my active orders" }
                    },
                    {
                        user: "{{user2}}",
                        content: { text: "Here are your active orders..." }
                    }
                ]
            ]
        },
        {
            name: "CANCEL_ORDER",
            similes: ["CANCEL", "REMOVE_ORDER", "DELETE_ORDER"],
            description: "Cancel a specific order",
            validate: async () => true,
            handler: async (
                runtime: IAgentRuntime,
                message: Memory,
                state: State | undefined,
                options?: Record<string, unknown>,
                callback?: HandlerCallback
            ): Promise<boolean> => {
                try {
                    const { tools } = await getPolymarketClient();
                    
                    let currentState = state ?? (await runtime.composeState(message));
                    currentState = await runtime.updateRecentMessageState(currentState);

                    const cancelContext = composeContext({
                        state: currentState,
                        template: `{{recentMessages}}

Extract the order ID to cancel from the recent messages. Look for:
- Order ID
- Order number
- Specific order reference`
                    });

                    const { object: params } = await generateObject({
                        runtime,
                        context: cancelContext,
                        modelClass: ModelClass.LARGE,
                        schema: z.object({
                            orderId: z.string().describe("Order ID to cancel")
                        })
                    });

                    const typedParams = params as { orderId: string };

                    const cancelTool = tools.find(tool => 
                        tool.name?.toLowerCase().includes('cancel')
                    );

                    if (!cancelTool) {
                        throw new Error("Cancel order tool not found");
                    }

                    const result = await cancelTool.execute?.(typedParams) || await cancelTool.call?.(typedParams);

                    const responseText = await generateResponse(
                        runtime,
                        currentState,
                        "CANCEL_ORDER",
                        result,
                        "Order cancelled successfully"
                    );

                    callback?.({ text: responseText, content: result });
                    return true;
                } catch (error) {
                    return handleError(error, "CANCEL_ORDER", callback);
                }
            },
            examples: [
                [
                    {
                        user: "{{user1}}",
                        content: { text: "Cancel order 67890" }
                    },
                    {
                        user: "{{user2}}",
                        content: { text: "Order 67890 has been cancelled" }
                    }
                ]
            ]
        },
        {
            name: "CANCEL_ALL_ORDERS",
            similes: ["CANCEL_ALL", "REMOVE_ALL_ORDERS", "DELETE_ALL_ORDERS"],
            description: "Cancel all active orders",
            validate: async () => true,
            handler: async (
                runtime: IAgentRuntime,
                message: Memory,
                state: State | undefined,
                options?: Record<string, unknown>,
                callback?: HandlerCallback
            ): Promise<boolean> => {
                try {
                    const { tools } = await getPolymarketClient();
                    
                    let currentState = state ?? (await runtime.composeState(message));
                    currentState = await runtime.updateRecentMessageState(currentState);

                    const cancelAllTool = tools.find(tool => 
                        tool.name?.toLowerCase().includes('cancel') &&
                        tool.name?.toLowerCase().includes('all')
                    );

                    if (!cancelAllTool) {
                        throw new Error("Cancel all orders tool not found");
                    }

                    const result = await cancelAllTool.execute?.({}) || await cancelAllTool.call?.({});

                    const responseText = await generateResponse(
                        runtime,
                        currentState,
                        "CANCEL_ALL_ORDERS",
                        result,
                        "All orders cancelled successfully"
                    );

                    callback?.({ text: responseText, content: result });
                    return true;
                } catch (error) {
                    return handleError(error, "CANCEL_ALL_ORDERS", callback);
                }
            },
            examples: [
                [
                    {
                        user: "{{user1}}",
                        content: { text: "Cancel all my orders" }
                    },
                    {
                        user: "{{user2}}",
                        content: { text: "All orders have been cancelled" }
                    }
                ]
            ]
        }
    ];

    return actions;
}

async function generateResponse(
    runtime: IAgentRuntime,
    state: State,
    actionName: string,
    result: unknown,
    successMessage: string
): Promise<string> {
    const responseTemplate = `
# Action Examples
{{actionExamples}}

# Knowledge
{{knowledge}}

# Task: Generate dialog and actions for the character {{agentName}}.
About {{agentName}}:
{{bio}}
{{lore}}

{{providers}}

{{attachments}}

# Capabilities
Note that {{agentName}} is capable of reading/seeing/hearing various forms of media, including images, videos, audio, plaintext and PDFs. Recent attachments have been included above under the "Attachments" section.

The action "${actionName}" was executed successfully.
${successMessage}

Here is the result:
${JSON.stringify(result, null, 2)}

{{actions}}

Respond to the message knowing that the action was successful and these were the previous messages:
{{recentMessages}}

Format the response in a conversational way, highlighting the most important information from the markets data. If showing multiple markets, summarize the key details like market names, current prices, and volume. Make the response engaging and informative.
`;

    const context = composeContext({ state, template: responseTemplate });
    
    return generateText({
        runtime,
        context,
        modelClass: ModelClass.LARGE,
    });
}

function handleError(
    error: unknown,
    actionName: string,
    callback?: HandlerCallback
): boolean {
    const errorMessage = error instanceof Error ? error.message : String(error);
    console.error(`Error executing ${actionName}:`, error);
    
    callback?.({
        text: `Error executing ${actionName}: ${errorMessage}`,
        content: { error: errorMessage },
    });
    
    return false;
}